@use "../util/functions.scss" as *;

$medium: 500;
$normal: 400;

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: rem(48);
  font-weight: $medium;
  line-height: rem(48);
}

h2 {
  font-size: rem(40);
  font-weight: $medium;
  line-height: rem(48);
  letter-spacing: rem(2);
}

h3 {
  font-size: rem(20);
  font-weight: $medium;
  line-height: rem(26);
  letter-spacing: rem(5);
}

body {
  font-weight: normal;
  font-size: rem(16);
  line-height: rem(26);
}

a,
a:visited,
a:active {
  text-decoration: none;
}
