html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

header {
  display: flex;
  justify-content: space-between;
  padding: 1.25rem;
  margin: 0 5%;
}
@media (min-width: 64em) {
  header {
    margin: 0 10%;
  }
}
header nav {
  display: none;
}
@media (min-width: 40em) {
  header nav {
    display: inline-block;
  }
  header nav ul {
    display: flex;
    gap: 1.25rem;
  }
  header nav ul li a {
    color: #323036;
  }
  header nav ul li a:hover {
    text-decoration: underline solid #323036;
  }
}
header .logo {
  display: flex;
  align-items: center;
}
header .logo:hover {
  cursor: pointer;
}
header .logo img {
  height: 27px;
}
header .hamburger {
  display: flex;
  align-items: center;
}
@media (min-width: 40em) {
  header .hamburger {
    display: none;
  }
}
header .menu {
  position: absolute;
  background-color: #1c1b1d;
  width: 100%;
  padding: 2.5rem;
  top: 9%;
  transform: translateX(-120%);
  transition: all 0.3s ease-in-out;
  z-index: 10;
}
header .menu ul {
  display: flex;
  flex-direction: column;
  font-size: 1.5625rem;
  gap: 1.875rem;
}
header .menu ul a {
  color: white;
}

footer {
  margin-top: 15.625rem;
  position: relative;
  padding: 2.5rem 0;
  padding-top: 9.375rem;
  background-color: #1c1b1d;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
footer .logo {
  margin: 0 auto;
}
footer .logo img {
  height: 27px;
}
footer .card {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: -20%;
  left: 5%;
  background-image: url("../../../assets/shared/desktop/bg-pattern-call-to-action.svg");
  background-size: cover;
  background-position: center;
  background-color: #e88069;
  border-radius: 0.9375rem;
  width: 90%;
  padding: 2.5rem;
  text-align: center;
  color: white;
}
footer .card .heading {
  font-size: 2rem;
}
footer .card .text {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  margin-bottom: 1.25rem;
}
@media (min-width: 64em) {
  footer .card .text {
    width: 50%;
  }
}
@media (min-width: 64em) {
  footer .card {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-align: left;
  }
}
footer .line {
  height: 0.125rem;
  width: 90%;
  margin: 0 auto;
  background-color: #323036;
}
footer nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #f0f2f4;
}
footer .navigation ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.625rem;
}
footer .navigation ul a {
  color: white;
}
footer .icons ul {
  display: flex;
  flex-direction: row;
  gap: 1.25rem;
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 3rem;
  font-weight: 500;
  line-height: 3rem;
}

h2 {
  font-size: 2.5rem;
  font-weight: 500;
  line-height: 3rem;
  letter-spacing: 0.125rem;
}

h3 {
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1.625rem;
  letter-spacing: 0.3125rem;
}

body {
  font-weight: normal;
  font-size: 1rem;
  line-height: 1.625rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

button {
  border: none;
  background-color: #f0f2f4;
  padding: 0.9375rem;
  border-radius: 0.625rem;
  cursor: pointer;
  font-family: "Jost", sans-serif;
  color: #323036;
  font-weight: 500;
}

