html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Jost", sans-serif;
}

@media (min-width: 40em) {
  .container {
    margin: 0 auto;
    width: 90%;
  }
}

.hero {
  border-radius: 0.9375rem;
  width: 100%;
  padding: 1.875rem 0;
  text-align: center;
  background-color: #e88069;
  background-image: url("../../../assets/home/desktop/bg-pattern-hero-home.svg");
}

.wrapper {
  width: 90%;
  display: flex;
  flex-direction: column;
  margin: 20% auto;
  color: white;
}
@media (min-width: 64em) {
  .wrapper {
    flex-direction: row;
    align-items: center;
    margin: 0 auto;
  }
}
.wrapper .text {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
.wrapper h2 {
  font-size: 2rem;
  letter-spacing: 0;
  line-height: 2.25rem;
}
.wrapper .hero-phone {
  background-image: url("../../../assets/home/desktop/image-hero-phone.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  width: 100%;
  min-height: 35rem;
  margin-bottom: -25%;
}
@media (min-width: 64em) {
  .wrapper .hero-phone {
    margin: 0 auto;
    background-position: center center;
    min-height: 43.75rem;
  }
}

.cards {
  width: 90%;
  margin: 5rem auto;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.values {
  width: 95%;
  margin: 0 auto;
  padding: 2.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 3.75rem;
}
@media (min-width: 64em) {
  .values {
    flex-direction: row;
  }
}
.values .value {
  display: flex;
  flex-direction: column;
}
.values .value .image {
  background-color: #f0f2f4;
  border-radius: 50%;
}
@media (min-width: 40em) {
  .values .value {
    flex-direction: row;
    gap: 2.5rem;
    text-align: left;
  }
}
@media (min-width: 64em) {
  .values .value {
    flex-direction: column;
    text-align: center;
  }
}
.values .text h3 {
  margin: 1.25rem 0;
}